.header{
    background-color: rgb(40, 40, 41);
    width:100%;
}

.nav{
    width:90%;
    height:50px;
    margin:auto 5%;
    display:flex;
    justify-content: space-between;
    align-items: center;
}

.nav h3{
    margin-top:0px;
    margin-bottom: 0px;
    color:white;
}

.nav button{
    background-color: gray;
    color:white;
    border:none;
    padding:5px 10px;
    border-radius:2px;
}