.log{
    height:300px;
    width:400px;
    background-color: aliceblue;
    margin:0px auto;
    border-radius: 5px;
}

.log h1{
    color:#4267b2;
    padding-top:10px;
  }

  .log label{
      padding-right:10px;
      font-size:20px;
  }

  .log input{
      padding:5px;
      font-size: 20px;
      border-radius: 5px;
      border:1px solid rgba(128, 128, 128, 0.39);
      color:rgb(58, 57, 57);
      width:300px;
   }

  .log table{
      margin-left:30px;
  }

  .log table tr{
    display:block;
    margin-top:2px;
  }

  .log table tr input[type = "submit"]{
      background-color:#4267b2;
      color:white;
      margin-top:10px;
      padding:5px 20px;
      width:120px;
      cursor: pointer;
  }
  